html,
body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.App {
  height: 100vh;
  margin: 0 auto;
}

.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  background: indianred;
  font-family: "roboto";
  font-size: 1.2em;
  padding: 0.8em;
}

.navbar > a {
  text-decoration: none;
  margin-right: 5%;
  color: whitesmoke;
}

.home {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.task-list {
  list-style-type: none;
}

.task-card {
  display: flex;
  flex-direction: column;
}

#task:hover {
  text-decoration-line: line-through;
  cursor: pointer;
}

.home .date {
  color: darkgray;
  font-size: 0.8em;
}
